import React from 'react';

class FinalStates extends React.Component {

  constructor(props) {
    super(props);

    this.deleteFinalState = this.deleteFinalState.bind(this);
  }

  deleteFinalState(event) {
    const stateToDelete = event.target.getAttribute("datavalue");
    this.props.deleteFinalState(stateToDelete);
  }

  render() {
    if (this.props.finalStates < 1) {
      return (
        <div className="text-muted">Nenhum estado adicionado</div>
      )
    }
    return (
      <div>
        {this.props.finalStates.map((item, index) => (
          <kbd datavalue={item} onClick={this.deleteFinalState} className="automatta-symbol" key={index}>{item}</kbd>
        ))}
      </div>
    );
  }
}

export default FinalStates;