import React from 'react';

class Alphabet extends React.Component {

  constructor(props) {
    super(props);

    this.deleteSymbolFromAlphabet = this.deleteSymbolFromAlphabet.bind(this);
  }

  deleteSymbolFromAlphabet(event) {
    const symbolToDelete = event.target.getAttribute("datavalue");
    this.props.deleteSymbolFromAlphabet(symbolToDelete);
  }

  render() {
    if (this.props.alphabet < 1) {
      return (
        <div className="text-muted">Nenhum símbolo adicionado</div>
      )
    }
    return (
      <div>
        {this.props.alphabet.map((item, index) => (
          <kbd datavalue={item} onClick={this.deleteSymbolFromAlphabet} className="automatta-symbol" key={index}>{item}</kbd>
        ))}
      </div>
    );
  }
}

export default Alphabet;