import React from 'react';

class Transitions extends React.Component {

  render() {
    if (this.props.transitions < 1) {
      return (
        <div className="text-muted">Nenhuma transição adicionado</div>
      )
    }
    return (
      <div>
        {this.props.transitions.map((item, index) => (
          <span className="alphabet-symbol" key={index}>
            Quando em <kbd>{item.from}</kbd> ler <kbd>{item.symbol}</kbd> ir para <kbd>{item.to}</kbd>
            <br/>
          </span>
        ))}
      </div>
    );
  }
}

export default Transitions;