import React from 'react';
import $ from 'jquery';
import Swal from 'sweetalert2';

import './App.css';

import Alphabet from './Alphabet.js'
import States from './States.js'
import FinalStates from './FinalStates.js'
import Transitions from './Transitions.js'


class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      alphabet: ["a", "b"],
      states: ["q0", "q1"],
      initialState: "q0",
      finalStates: ["q1"],
      transitions: [
        {
          "identifier": "q0aq1",
          "from": "q0",
          "symbol": "a",
          "to": "q1"
        },
        {
          "identifier": "q1aq0",
          "from": "q1",
          "symbol": "b",
          "to": "q0"
        }
      ],
      notFinalStates: []
    };

    this.addSymbolToAlphabet = this.addSymbolToAlphabet.bind(this);
    this.deleteSymbolFromAlphabet = this.deleteSymbolFromAlphabet.bind(this);

    this.deleteState = this.deleteState.bind(this);
    this.addState = this.addState.bind(this);

    this.addFinalState = this.addFinalState.bind(this);
    this.deleteFinalState = this.deleteFinalState.bind(this);

    this.addTransition = this.addTransition.bind(this);
    this.printAll = this.printAll.bind(this);

    this.testWord = this.testWord.bind(this);
    this.transitionExists = this.transitionExists.bind(this);

  }

  addSymbolToAlphabet() {
    let symbolAdded = $("#symbol").val();
    let alphabet = this.state.alphabet;
    let isNewSymbol = true;

    for (let i=0; i < alphabet.length; i++) {
      if (symbolAdded === alphabet[i])
        isNewSymbol = false;
        $("#symbolError").html("Símbolo já adicionado");
        $("#symbolSuccess").html("");
        $("#symbol").val("");
        $("#symbol").focus();
    }
    
    if (symbolAdded.length < 1) {
      $("#symbol").val("");
      $("#symbol").focus();
      $("#symbolError").html("Está vazio!");
      $("#symbolSuccess").html("");
    } else if (isNewSymbol) {
      alphabet.push(symbolAdded);

      this.setState({
        "alphabet": alphabet
      });

      $("#symbol").val("");
      $("#symbol").focus();
      $("#symbolError").html("");
      $("#symbolSuccess").html("Símbolo adicionado com sucesso");
    }
  }

  deleteSymbolFromAlphabet(selectedSymbol) {
    let alphabet = this.state.alphabet;
    let transitions = this.state.transitions;
    const removedSymbolIndex = alphabet.indexOf(selectedSymbol);

    alphabet.splice(removedSymbolIndex, 1);

    let stateDetectedList = [];

    for (let i = 0; i < transitions.length; i++) {
      if (transitions[i].symbol === selectedSymbol) {
        stateDetectedList.push(transitions[i])
      }
    }

    for (let i = 0; i < stateDetectedList.length; i++) {
      let temporaryIndex = transitions.indexOf(stateDetectedList[i]);
      transitions.splice(temporaryIndex, 1);
    }

    this.setState({
        "alphabet": alphabet,
        "transitions": transitions
      });
  }

  printAll() {
    console.log(this.state)
  }

  addState() {
    let stateAdded = $("#state").val();
    let states = this.state.states;
    let notFinalStates = this.state.notFinalStates;
    let isNewState = true;
    let isInitialState = document.getElementById('initialState').checked;

    for (let i=0; i < states.length; i++) {
      if (stateAdded === states[i])
        isNewState = false;
        $("#stateError").html("Estado já adicionado");
        $("#stateSuccess").html("");
        $("#state").val("");
        $("#state").focus();
    }

    if (stateAdded.length < 1) {
      $("#state").val("");
      $("#state").focus();
      $("#stateError").html("Está vazio!");
      $("#stateSuccess").html("");
    } else if (isNewState) {
      states.push(stateAdded);
      notFinalStates.push(stateAdded);

      this.setState({
        "states": states,
        "notFinalStates": notFinalStates
      });

      if (isInitialState) {
        this.setState({
          "initialState": stateAdded
        });
      }

      $("#state").val("");
      $("#state").focus();
      $("#stateError").html("");
      $("#stateSuccess").html("Estado adicionado com sucesso");
    }
  }

  deleteState(selectedState) {
    let states = this.state.states;
    let finalStates = this.state.finalStates;
    let notFinalStates = this.state.notFinalStates;
    let transitions = this.state.transitions;

    const removedStateIndex = states.indexOf(selectedState);

    states.splice(removedStateIndex, 1);

    if (finalStates.indexOf(selectedState) > -1) {
      finalStates.splice(finalStates.indexOf(selectedState), 1);
    }

    if (notFinalStates.indexOf(selectedState) > -1) {
      notFinalStates.splice(notFinalStates.indexOf(selectedState), 1);
    }

    let stateDetectedList = [];

    for (let i = 0; i < transitions.length; i++) {
      if (transitions[i].from === selectedState || transitions[i].to === selectedState) {
        stateDetectedList.push(transitions[i])
      }
    }

    for (let i = 0; i < stateDetectedList.length; i++) {
      let temporaryIndex = transitions.indexOf(stateDetectedList[i]);
      transitions.splice(temporaryIndex, 1);
    }

    this.setState({
      "states": states,
      "finalStates": finalStates,
      "notFinalStates": notFinalStates,
      "transitions": transitions
    });

    if (this.state.initialState === selectedState) {
      this.setState({
        "initialState": states[0]
      })
    }
  }

  addFinalState() {
    let finalStates = this.state.finalStates;
    let notFinalStates = this.state.notFinalStates;

    let addedState = $("#finalState").val();

    if (addedState !== "none") {
      finalStates.push(addedState);
      let newFinalStateIndex = notFinalStates.indexOf(addedState);
      notFinalStates.splice(newFinalStateIndex, 1);

      this.setState({
        "finalStates": finalStates,
        "notFinalStates": notFinalStates
      })
    }
  }

  deleteFinalState(selectedState) {
    let finalStates = this.state.finalStates;
    let notFinalStates = this.state.notFinalStates;

    const removedStateIndex = finalStates.indexOf(selectedState);

    finalStates.splice(removedStateIndex, 1);
    notFinalStates.push(selectedState)

    this.setState({
      "finalStates": finalStates,
      "notFinalStates": notFinalStates
    });
  } 

  addTransition() {
    let fromState = $("#fromState").val();
    let toState = $("#toState").val();
    let symbolRead = $("#symbolRead").val();
    let identifier = fromState + symbolRead + toState;
    let isNewTransition = true;

    let currentTransitions = this.state.transitions;

    if (fromState !== "none" && toState !== "none" && symbolRead !== "none") {
      for (let i = 0; i < currentTransitions.length; i++) {
        if (identifier === currentTransitions[i].identifier) {
          isNewTransition = false;
        }
      }

      if (isNewTransition) {

        let newTransition = {
          "identifier": identifier,
          "from": fromState,
          "symbol": symbolRead,
          "to": toState
        }

        currentTransitions.push(newTransition)

        this.setState({
          "transitions": currentTransitions
        });
      }
    }
  }

  transitionExists(fromState, symbol) {
    const transitions = this.state.transitions;

    let result = {
      "result": false,
      "transition": fromState
    };

    for (let i = 0; i < transitions.length; i++) {
      if (transitions[i].from === fromState && transitions[i].symbol === symbol) {
        result.result = true;
        result.transition = transitions[i];
      }
    }

    return result;
  }

  testWord() {
    const transitions = this.state.transitions;
    const alphabet = this.state.alphabet;
    const finalStates = this.state.finalStates;
    const initialState = this.state.initialState;

    let testedWord = $("#testedWord").val();
    const splitWord = testedWord.split('');

    let t = this.transitionExists(initialState, splitWord[0]);
    
    if (t.result === false) {
      Swal.fire({
        title: 'Opa!',
        html: 'O autômato não aceita a palavra: <br><strong>' + testedWord + '</strong><br><br> Paramos no estado <kbd>' + initialState + '</kbd>.',
        type: 'error',
        confirmButtonText: 'OK',
        customClass: {
          confirmButton: 'btn btn-info'
        },
        buttonsStyling: false
      })
      return false;
    }

    for (let i = 1; i < splitWord.length; i++) {
      t = this.transitionExists(t.transition.to, splitWord[i]);
    
      if (t.result === false) {
        const stoppingState = t.transition.to !== undefined ? t.transition.to : t.transition;
        Swal.fire({
          title: 'Opa!',
          html: 'O autômato não aceita a palavra: <br><strong>' + testedWord + '</strong><br><br> Paramos no estado <kbd>' + stoppingState + '</kbd>.',
          type: 'error',
          confirmButtonText: 'OK',
          customClass: {
            confirmButton: 'btn btn-info'
          },
          buttonsStyling: false
        })
        return false;
      }
    }

    if (finalStates.indexOf(t.transition.from) > -1) {
      Swal.fire({
        title: 'Sucesso',
        html: 'O autômato aceita a palavra <br><strong>' + testedWord + '</strong><br><br> Paramos no estado <kbd>' + t.transition.to + '</kbd>.',
        type: 'success',
        confirmButtonText: 'OK',
        customClass: {
          confirmButton: 'btn btn-info'
        },
        buttonsStyling: false
      })
      return true;
    }

    const stoppingState = t.transition.to !== undefined ? t.transition.to : t.transition;

    Swal.fire({
      title: 'Opa!',
      html: 'O autômato não aceita a palavra: <br><strong>' + testedWord + '</strong><br><br> Paramos no estado <kbd>' + stoppingState + '</kbd>.',
      type: 'error',
      confirmButtonText: 'OK',
      customClass: {
        confirmButton: 'btn btn-info'
      },
      buttonsStyling: false
    })
    return false;
  }

  render() {
    return (
      <div className="container-fluid mt-2">
        <div className="row">
          <div className="col-4 text-center">
            <h5>Alfabeto</h5>
            <Alphabet deleteSymbolFromAlphabet={this.deleteSymbolFromAlphabet} alphabet={this.state.alphabet}/>
            <button type="button" data-toggle="modal" data-target="#alphabetModal" className="btn mt-2 btn-dark">Adicionar símbolo</button>
            <br/><small className="text-muted">Clique em um símbolo para removê-lo</small>
          </div>
          <div className="col-4 text-center">
            <h5>Estados</h5>
            <States initialState={this.state.initialState} deleteState={this.deleteState} states={this.state.states}/>
            <button type="button" data-toggle="modal" data-target="#stateModal" className="btn mt-2 btn-dark">Adicionar estado</button>
            <br/><small className="text-muted">Clique em um estado para removê-lo</small>
          </div>
          <div className="col-4 text-center">
            <h5>Estados Finais</h5>
            <FinalStates deleteFinalState={this.deleteFinalState} finalStates={this.state.finalStates}/>
            <button type="button" data-toggle="modal" data-target="#finalStateModal" className="btn mt-2 btn-dark">Adicionar estado final</button>
            <br/><small className="text-muted">Clique em um estado final para removê-lo</small>
          </div>
        </div>
        <div className="row">
          <div className="col-8 text-center">
            <h5>Função de transição</h5>
            <Transitions transitions={this.state.transitions}/>
            <button type="button" data-toggle="modal" data-target="#transitionModal" className="btn mt-2 btn-dark">Adicionar transição</button>
          </div>
          <div className="col-4 text-center">
            <h5>Palavra</h5>
            <input type="text" className="form-control" id="testedWord" placeholder="Ex.: aabdc"/>
            <button type="button" onClick={this.testWord} className="btn mt-2 btn-dark">Testar palavra</button>
          </div>
        </div>

        <div className="modal fade" id="alphabetModal" tabIndex="-1" role="dialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Adicionar símbolo</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <div className="form-group">
                  <input maxLength="1" type="text" className="form-control" id="symbol" placeholder="Ex.: a"/>
                  <small id="symbolError" className="text-danger"></small>
                  <small id="symbolSuccess" className="text-success"></small>
                  <small className="form-text text-muted">Use apenas um símbolo</small>
                </div>
              </div>
              <div className="modal-footer">
                <button onClick={this.addSymbolToAlphabet} type="button" className="btn btn-info">Adicionar</button>
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
              </div>
            </div>
          </div>
        </div>

        <div className="modal fade" id="stateModal" tabIndex="-1" role="dialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Adicionar estado</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <div className="form-group">
                  <input maxLength="2" type="text" className="form-control" id="state" placeholder="Ex.: a"/>
                  <small id="stateError" className="text-danger"></small>
                  <small id="stateSuccess" className="text-success"></small>
                  <small className="form-text text-muted">Use até dois símbolos</small>
                  <div className="form-check">
                    <input className="form-check-input" type="checkbox" value="" id="initialState"/>
                    <label className="form-check-label" htmlFor="initialState">
                      Estado inicial
                    </label>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button onClick={this.addState} type="button" className="btn btn-info">Adicionar</button>
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
              </div>
            </div>
          </div>
        </div>

        <div className="modal fade" id="finalStateModal" tabIndex="-1" role="dialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Adicionar estado final</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <div className="form-group">
                  <select id="finalState" defaultValue="none" className="custom-select">
                    <option value="none"> Selecione um estado </option>
                    {this.state.notFinalStates.map((item, index) => (
                      <option value={item} className="automatta-symbol" key={index}>{item}</option>
                    ))}
                  </select>
                </div>
              </div>
              <div className="modal-footer">
                <button onClick={this.addFinalState} type="button" className="btn btn-info">Adicionar</button>
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
              </div>
            </div>
          </div>
        </div>

        <div className="modal fade" id="transitionModal" tabIndex="-1" role="dialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Adicionar transição</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <div className="form-group">
                  <div className="input-group align-items-center">
                    Quando em
                     <select id="fromState" defaultValue="none" className="custom-select ml-1 mr-1">
                      <option value="none"> -- </option>
                      {this.state.states.map((item, index) => (
                        <option value={item} className="automatta-symbol" key={index}>{item}</option>
                      ))}
                    </select>
                    ler
                     <select id="symbolRead" defaultValue="none" className="custom-select ml-1 mr-1">
                      <option value="none"> -- </option>
                      {this.state.alphabet.map((item, index) => (
                        <option value={item} className="automatta-symbol" key={index}>{item}</option>
                      ))}
                    </select>
                    ir para
                     <select id="toState" defaultValue="none" className="custom-select ml-1 mr-1">
                      <option value="none"> -- </option>
                      {this.state.states.map((item, index) => (
                        <option value={item} className="automatta-symbol" key={index}>{item}</option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button onClick={this.addTransition} type="button" className="btn btn-info">Adicionar</button>
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
