import React from 'react';

class States extends React.Component {

  constructor(props) {
    super(props);

    this.deleteState = this.deleteState.bind(this);
    this.renderStates = this.renderStates.bind(this);
  }

  deleteState(event) {
    const stateToDelete = event.target.getAttribute("datavalue");
    this.props.deleteState(stateToDelete);
  }

  renderStates(){
    const states = this.props.states;
    const initialState =  this.props.initialState;
    let statesList = [];

    if (states.length < 1) {
      return (
        <span className="text-muted">Nenhum estado adicionado</span>
      )
    }

    for (var i = 0; i < states.length; i++) {
      if (states[i] === initialState) {
        statesList.push(
          <kbd datavalue={states[i]} onClick={this.deleteState} className="automatta-symbol initial" key={i}>{states[i]}</kbd>)
      } else {
        statesList.push(
          <kbd datavalue={states[i]} onClick={this.deleteState} className="automatta-symbol" key={i}>{states[i]}</kbd>)
      }
    }

    return statesList;
  }

  render() {
    return (
      <div>
        {this.renderStates()}
      </div>
    );
  }
}

export default States;